import React from "react"
import PropTypes from 'prop-types'
import styled from 'styled-components'

const PlantWrapper = styled.div`
  text-align: center;
`

const Title = styled.h1`
  font-family: Montserrat, sans serif;
`

const MaxKwp = styled.h3`
`

const CurrentlyProducingWrapper = styled.h6`
  font-size: 12px;
`

const CurrentlyConsumingWrapper = styled.h6`
  font-size: 12px;
`

export default function Plant(props) {
  const { plantName, maxKwp, currentlyProducing, currentlyConsuming } = props;
  return (
    <PlantWrapper>
      <Title>{plantName}</Title>
      {maxKwp ? <MaxKwp>Kilo Watt Peak: { maxKwp }</MaxKwp> : null}
      {currentlyProducing ? <CurrentlyProducingWrapper>Currently Producing: { currentlyProducing }</CurrentlyProducingWrapper> : null}
      {currentlyConsuming ? <CurrentlyConsumingWrapper>Currently Consuming: { currentlyConsuming }</CurrentlyConsumingWrapper> : null}
    </PlantWrapper>
  )
}

Plant.propTypes = {
  plantName: PropTypes.string.isRequired,
  maxKwp: PropTypes.number,
  currentlyProducing: PropTypes.number,
  currentlyConsuming: PropTypes.number,
}

Plant.defaultProps = {
  maxKwp: 666,
  currentlyProducing: 666,
  currentlyConsuming: 777,
}



